.Person {
  font-family: Arial, Helvetica, sans-serif;
  width: 250px;
  margin-bottom: 16px;
  padding: 8px;

  text-align: center;
  border: 1px solid #000;
  border-radius: 8px;

  &__name {
    margin-top: 8px;
  }

  &__age {
    color: darkviolet;
    font-weight: bold;
  }

  &__partner {
    font-size: 18px;
  }
}
